import React, {useState} from 'react';
import {ThemeProvider} from '@mui/material/styles';
import {CssBaseline} from '@mui/material';
import theme from './themes';
import { CircleSpinnerOverlay, FerrisWheelSpinner } from 'react-spinner-overlay'
import Header from './components/Header'
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import FileDropper from './components/FileDropper';
import JsonResultViewer from './components/JsonResultViewer';
import axios from 'axios';


function App() {
    const [json, setJson] = useState(undefined);
    const [isLoading, setIsLoading] = useState(false);

    return (
        <ThemeProvider theme={theme}>
            <CircleSpinnerOverlay
                loading={isLoading}
                overlayColor="rgba(0,153,255,0.2)"
            />
            {/* CssBaseline kickstart an elegant, consistent, and simple baseline to build upon. */}
            <CssBaseline/>
            <Header/>
            <Box sx={{flexGrow: 1, padding: 5, height: '80vh'}}>
                <Grid container spacing={10} sx={{height: '100%'}}>
                    <Grid item xs={6} sx={{height: '100%'}}>
                        <FileDropper filePassed={file => {
                            let formData = new FormData()
                            formData.append("file", file)
                            setIsLoading(true)
                            axios.post<FormData>("/api/entrypoint", formData).then(
                                (response: { data: any; }) => {
                                    console.log(response)
                                    setIsLoading(false)
                                    setJson(response.data)
                                }
                            ).catch(function (error) {
                                setJson(undefined)
                                setIsLoading(false)
                                if (error.response) {
                                    console.log(error.response.data);
                                    console.log(error.response.status);
                                    console.log(error.response.headers);
                                } else if (error.request) {
                                    console.log(error.request);
                                } else {
                                    console.log('Error', error.message);
                                }
                            })
                        }}/>
                    </Grid>
                    <Grid item xs={6} sx={{height: '100%'}}>
                        <JsonResultViewer jsonData={json}/>
                    </Grid>
                </Grid>
            </Box>
        </ThemeProvider>
    );
}

export default App;
